.item {
	width: 100%;
	display: flex;
	flex-direction: column;
	background: #1c1c21;
	box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06), 0px 0px 1px rgba(0, 0, 0, 0.04);
	margin: 0 8px 24px auto;
	border-radius: 40px;
	padding: 24px;

	box-sizing: border-box;
}

.itemID {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 100%;
}

.timestemp {
	display: flex;
	align-items: center;
	text-align: right;
}

.itemInfo {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.name {
	max-width: 536px;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}
.status {
	color: #00cccc;
}

.boxImagePrice {
	height: 64px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	box-sizing: border-box;
}

.fixBox {
	display: flex;
}

.itemIamge {
	width: 64px;
	height: 64px;
	border-radius: 100px;
	border: 2px solid #801ab3;
	background: #131316;
	z-index: 2;
	position: relative;
	box-sizing: border-box;
	margin-right: -24px;
}
.image {
	width: 64px;
	height: 100%;
	object-fit: cover;
}

.price {
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	box-sizing: border-box;
}

.count {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 3;
}
